<template>
    <div class="Login">
       <Header :FlagLogin=true :FlagSeek=false :FlagMine=false></Header>
        <main class="main">
            <div class="city">
                <div class="login-input">
                <input v-model="username"  placeholder="账号" ></input>
                </div>
                <div class="login-input">
                <!-- <input placeholder="请输入密码" v-model="password"></input> -->
                <input :type="value?'text':'password'" v-model="password" placeholder="密码">
                  <div class="login-key">
                    <van-switch  v-model="value"  size="0.2rem"  active-color="#07c160"  inactive-color="#f44"/>
                    <!-- <van-switch v-model="checked" size="0.4rem"/> -->
                  </div>
                </div>
                
                <div class="login-input">
                <input v-model="loginyz" placeholder="验证码" ></input>
                <div class="yanzheng">
                  <div class="yz-img">
                    <img :src="code" alt="">
                  </div>
                  <div class="yz-text" @click="add()">
                    <span>看不清</span>
                    <p>换一个</p>
                  </div>
                  
                </div>
                </div>
                <div class="login-text">
                    <span>温馨提示:未注册的账号，登录时将自动注册</span>
                    <p>注册过的用户可凭账号密码登录</p>
                </div>
                <div class="login-button">
                  <van-button type="primary" @click="submit()">登录</van-button>
                
                </div>
                <div class="login-reset">
                    <span>重置密码？</span>
                </div>
            </div>
        </main>
    </div>
</template>

<script>
    import Header from "./../Unify/Header"
    import axios from "axios"
    axios.defaults.withCredentials=true;
    export default { 
      mounted() {
             this.add()
           
           }, 
      components:{
          Header
        },
        data(){
            return{
                username:"",
                password:"",
                loginyz:"",
                value:false,
                code:""
            }
        },
         methods: {
            add(){
               this.$http.post(`http://elm.cangdu.org/v1/captchas`).then(res => {
                // console.log(res.data.code)
                if(res.data.code!=""){
                    this.code=res.data.code
                }
              })
           },
            //返回上次路由
            GoBack() {
            this.$router.go(-1);
            },
            submit(){
              const self = this;
                if (self.username == "" || self.password== ""){
        //                console.log("请输入账号或密码");
                  self.$toast({
                    message: "请输入账号或密码",
                    position: "bottom"
                  })
                }
                else if(self.loginyz == "" ){
                  //   console.log("请输入验证码");
                  self.$toast({
                    message: "请输入验证码",
                    position: "bottom"
                  })
                }
               else if (!(/^1[34578]\d{9}$/.test(self.username))){
                    console.log("账号格式错误");
                  self.$toast({
                    message: "账号格式错误",
                    position: "bottom"
                    
                  })
                }else{
                  /*接口请求*/
                this.$http.post('http://elm.cangdu.org/v2/login',{
                        username:this.username,
                        password:this.password,
                        captcha_code:this.loginyz,
                }).then((res)=>{
                          if(res.data.id){
                //    localStorage.setItem('Code',JSON.stringify(res.data))
                        this.$store.commit("fang",res.data)
                          this.$router.push('/mine')
                          }else{
                              alert(res.data.message)
                          }
                    })
                  }
                  
            
            }   
              
          },

          
    }
    
</script>

<style lang="scss" scoped>
.Login {
  height: 100%;
  margin: 0;
  padding: 0;
  background: #f4f4f4;
}
.Login .main {
  width: 100%;
  margin-top: 1rem;

}

.Login .main .city .login-input {
  position: relative;
 input{
   width: 100%;
   height: 0.4rem;
   border-top: solid 1px #fff;
   border-left: solid 1px #fff;
   padding: 0 0.2rem;
 }
}
.Login .main .city .login-input .login-key {
  position:absolute;
  right: 25px;
  top: 7px;

}
.Login .main .city .login-input .yanzheng{
  width: 1.5rem;
  height: 0.4rem;
  display: flex;
  position: absolute;
  right: 18px;
  bottom: 5px;
  justify-content:space-around ;
 
}
.Login .main .city .login-input .yanzheng .yz-img{
  width: 60%;
  height: 0.4rem;
  img{
    display: block;
    width: 100%;
    margin-top: 8px;
  }
  /* background: yellow; */
}
.Login .main .city .login-input .yanzheng .yz-text{
  width: 35%;
  height: 0.4rem;
  font-size: 0.14rem;
  p{
    color: #3190e8;
  }
}
.Login .main .city .login-button {
  padding: 0 0.2rem;
}
.Login .main .city input {
  height: 0.5rem;
  font-size: 0.2rem;

}
.Login .main .city .van-button {
  width: 100%;
  height: 0.45rem;
  font-size: 0.2rem;
}
.Login .main .city .login-text {
  margin-bottom: 0.2rem;
  padding: 0 0.2rem;
  color: red;
  font-size: 0.18rem;
}
.Login .main .city .login-reset{
    width: 100%;
    height: auto;
    margin-top: 0.2rem;
    text-align: right;
}
.Login .main .city .login-reset span{
    padding:  0.2rem 0;
    color: #3190e8;
}

</style>